.intro-image {
    display: inline-block;
    height: 500px;
    width: initial;
    margin: 20px;
    -webkit-animation: fadePop 4s;
            animation: fadePop 4s;
}

@-webkit-keyframes fadePop {
    0% {
        opacity: 0;
    }
    60% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@keyframes fadePop {
    0% {
        opacity: 0;
    }
    60% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}



