.gallery-modal {
    height: 500px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    position: fixed;
    display: -ms-inline-flexbox;
    display: inline-flex;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    margin: auto;
    z-index: 1;
}

.image {
    height: inherit;
}

.button-close {
    margin: 20px;
}


