:root{
  --color1: #000000;
  --color2: #000000;
  --color3: #A3A3A3;
  --color4: #FFFFFF;
  --color5: #000000;
}

.main-app {
  text-align: center;
  -webkit-animation: fadein 3s;
          animation: fadein 3s;
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}


