.tab-container {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 2px;
    right: 0;
    margin-left: auto;
    margin-right: 20px
}

.bubble {
    display: inline-block;
    width: 3px;
    height: 3px;
    border: solid;
    border-radius: 100%;
    border-width: 1px;
    margin-bottom: 3px;
    background-color: var(--color1);
}


